import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const routes = [
  {
    name: 'default',
    path: '*',
    redirect: '/home'
  },
  {
    name: 'index',
    path: "/",
    redirect: '/deposit',
    component: resolve => require(['../index.vue'], resolve),
    children: [
      {
        path: '/airdrop',
        name: "airdrop",
        meta: {
          title: 'airdrop'
        },
        component: resolve => require(['../views/airdrop/index.vue'], resolve)
      },
      {
        path: '/deposit',
        name: "deposit",
        meta: {
          title: 'deposit'
        },
        component: resolve => require(['../views/deposit/index.vue'], resolve)
      },
      {
        path: '/depositDetail',
        name: "depositDetail",
        meta: {
          title: 'depositDetail'
        },
        component: resolve => require(['../views/deposit/detail.vue'], resolve)
      },
      // {
      //   path: '/Balance',
      //   name: "Balance",
      //   meta: {
      //     title: 'Balance'
      //   },
      //   component: resolve => require(['../views/balance/index.vue'], resolve)
      // },
      // {
      //   path: '/Instrumentpanel',
      //   name: "Instrumentpanel",
      //   meta: {
      //     title: 'Instrumentpanel'
      //   },
      //   component: resolve => require(['../views/Instrumentpanel.vue'], resolve)
      // }
    ]
  }
]


export default new Router({
  routes
})



